/* Index.tsx styles */
body { 
  overflow: auto;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* iOS places the subtitle above the title */
ion-card-header.ios {
  display: flex;
  flex-flow: column-reverse;
}
#contactChips {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.spacing {
  margin-top: 3em;
}


/* Images */
.starWarsImg {
  max-width: 100%;
}
.logoImg {
  max-width: 3em;
  margin-right: 2em;
}
.samPic {
  max-width: 100%;
  padding: 1em 12em;
}

/* Basic Link styles */
a:link {
  text-decoration: none;
}
/* a:visited {
  color: #9f9e9e;
}*/

a:hover {
  color: #D60000;
}

/* Navigation styles */
.normalNavContainer {
    display: flex;
}
#navGrid {
  margin-bottom: 3em;
}
.navbar-normal {
  display: flex;
  align-items: center;
}
.navbar-small-devices {
  display: flex;
}
.ion-menu-toggle {
  margin: 0 auto;
}


/* Customizing Nav Buttons */
ion-button {
  
  /* --background: #b4b4b4; */
  --background-hover: #7271712b;
  /* --background-activated: #ff2600;
  --background-focused: #f04444;

  --color: grey; */
  /* --box-shadow: 0 2px 6px 0 rgb(0, 0, 0, 0.25); */

  /* --ripple-color: deeppink; */

  /* --padding-top: 10px;
  --padding-bottom: 10px; */
}
/* Nav Responisve Sizing */
@media (min-width: 768px) {
  .navbar-small-devices {
      display: none;
  }
}
@media (max-width: 768px) {
  .navbar-normal {
      display: none;
  }
}

/* Dark mode styles */
#darkModeToggleButton {
  background: none;
}

.darkModeContainer {
  display: flex;
  justify-content: flex-end;
}

/* Adding dark mode colors */
.dark {
  background-color: #333;
  color: #fff;
}
.light {
  background-color: #fff;
  color: #333;
}
